import React, { useState, useEffect } from "react"; // useEffect ì¶”ê°€
import "./QuestionCard.css";

const QuestionCard = ({
  question,
  questionNumber,
  totalQuestions,
  overallNumber,
  overallTotal,
  onAnswer,
}) => {
  const { text, type, options, section } = question;
  
  // [ìˆ˜ì • 1] Multi íƒ€ì… ì—¬ë¶€ í™•ì¸ (ëŒ€ì†Œë¬¸ì ë¬´ê´€)
  const isMulti = type?.toLowerCase().includes("multi");

  // [ìˆ˜ì • 2] ìƒíƒœ ì´ˆê¸°í™”: Multií˜•ì´ë©´ ë°°ì—´([]), ì•„ë‹ˆë©´ ë¬¸ìì—´("")
  const [selected, setSelected] = useState(isMulti ? [] : "");

  // ì§ˆë¬¸ì´ ë°”ë€” ë•Œë§ˆë‹¤ state ì´ˆê¸°í™” (useEffect ì‚¬ìš© ê¶Œì¥)
  useEffect(() => {
    setSelected(isMulti ? [] : "");
  }, [question, isMulti]);

  /** -------------------------------
   * Likert Scale ì˜µì…˜
   --------------------------------*/
  const likertScale = [
    "ğŸ˜ Strongly Agree",
    "ğŸ™‚ Agree",
    "ğŸ˜ Neutral",
    "ğŸ˜• Disagree",
    "ğŸ˜¡ Strongly Disagree",
  ];

  const finalOptions =
    type?.toLowerCase() === "likert"
      ? likertScale
      : normalizeOptions(options);

  // [ìˆ˜ì • 3] ì˜µì…˜ í´ë¦­ í•¸ë“¤ëŸ¬ (ë‹¤ì¤‘ ì„ íƒ vs ë‹¨ì¼ ì„ íƒ ë¶„ê¸°)
  const handleOptionClick = (opt) => {
    if (isMulti) {
      // ì´ë¯¸ ì„ íƒëœ ë°°ì—´ì¸ì§€ í™•ì¸
      if (selected.includes(opt)) {
        // ìˆìœ¼ë©´ ì œê±° (í† ê¸€)
        setSelected(selected.filter((item) => item !== opt));
      } else {
        // ì—†ìœ¼ë©´ ì¶”ê°€
        setSelected([...selected, opt]);
      }
    } else {
      // ë‹¨ì¼ ì„ íƒ
      setSelected(opt);
    }
  };

  // [ìˆ˜ì • 4] ë²„íŠ¼ í™œì„±í™” ì¡°ê±´ ì²´í¬
  const isNextActive = isMulti ? selected.length > 0 : !!selected;

  return (
    <div className="question-card fade-in">
      {/* ... ìƒë‹¨ ì •ë³´ (ê¸°ì¡´ ë™ì¼) ... */}
      <div className="q-header">
        <div className="q-section">{section}</div>
        <div className="q-progress-group">
          <div className="q-progress-chapter">
            Chapter: {questionNumber} / {totalQuestions}
          </div>
          <div className="q-progress-overall">
            Overall: {overallNumber} / {overallTotal}
          </div>
        </div>
      </div>

      {/* ì§ˆë¬¸ í…ìŠ¤íŠ¸ */}
      <h2 className="q-text">
        {text} 
        {/* Multií˜•ì¼ ê²½ìš° íŒíŠ¸ í‘œì‹œ */}
        {isMulti && <span style={{fontSize:'0.7em', color:'#666', marginLeft:'10px'}}>(Select all that apply)</span>}
      </h2>

      {/* ---------- ê°ê´€ì‹/Multi ì˜µì…˜ ---------- */}
      {type !== "text" && (
        <div className="q-options">
          {finalOptions.map((opt, idx) => {
            // [ìˆ˜ì • 5] ì„ íƒ ìƒíƒœ í™•ì¸ ë¡œì§ ë³€ê²½
            const isSelected = isMulti 
              ? selected.includes(opt) 
              : selected === opt;

            return (
              <button
                key={idx}
                className={`q-option ${isSelected ? "selected" : ""}`}
                onClick={() => handleOptionClick(opt)}
              >
                {/* ë‹¤ì¤‘ ì„ íƒ ì‹œ ì²´í¬ë°•ìŠ¤ ëŠë‚Œì˜ ì´ëª¨ì§€ ì¶”ê°€ ê°€ëŠ¥ */}
                {isMulti && (isSelected ? "âœ… " : "â¬œ ")}
                {opt}
              </button>
            );
          })}
        </div>
      )}

      {/* ---------- ì£¼ê´€ì‹ ì…ë ¥ ---------- */}
      {type === "text" && (
        <textarea
          className="q-textarea"
          placeholder="Write your answer..."
          value={selected}
          onChange={(e) => setSelected(e.target.value)}
        />
      )}

      {/* ---------- Next ë²„íŠ¼ ---------- */}
      <div className="q-footer">
        <button
          className={`next-btn ${isNextActive ? "active" : ""}`}
          disabled={!isNextActive}
          onClick={() => {
            if (!isNextActive) return;
            
            // ê²°ê³¼ ì „ë‹¬ (Multiì˜ ê²½ìš° ë°°ì—´ì„ joiní•´ì„œ ë³´ë‚´ê±°ë‚˜ ë°°ì—´ ê·¸ëŒ€ë¡œ ì „ë‹¬)
            // Firebase ì €ì¥ì„ ìœ„í•´ ë°°ì—´ ê·¸ëŒ€ë¡œ ì „ë‹¬í•˜ê±°ë‚˜, í•„ìš”ì‹œ .join(", ") ì‚¬ìš©
            onAnswer(selected); 
            
            // ì´ˆê¸°í™”ëŠ” useEffectì—ì„œ ì²˜ë¦¬í•˜ë¯€ë¡œ ì—¬ê¸°ì„œëŠ” ìƒëµ ê°€ëŠ¥í•˜ë‚˜ ì•ˆì „ì„ ìœ„í•´
            // setSelected(isMulti ? [] : ""); 
          }}
        >
          Next â†’
        </button>
      </div>
    </div>
  );
};

// [ìˆ˜ì • 6] normalizeOptions ì •ê·œì‹ë„ ë™ì¼í•˜ê²Œ ê°•í™”
const normalizeOptions = (options) => {
  if (!options) return [];
  if (Array.isArray(options)) return options;

  if (typeof options === "string") {
    // ì¤„ë°”ê¿ˆ, ì„¸ë¯¸ì½œë¡  í¬í•¨
    return options
      .split(/[\|,\n;\r]+/)
      .map((o) => o.trim())
      .filter((o) => o.length > 0);
  }
  return [];
};

export default QuestionCard;